"use client"

import ApiContext from '@/app/Context/ProductApi/ApiContext';
import Link from 'next/link';
import { useParams } from 'next/navigation';
import React, { useContext, useEffect, useState } from 'react'
  
  function CategoryPage() {
    const params = useParams();
    const {categorySlug} = params;
    const {products} = useContext(ApiContext)
    const [categoryProduct , setCategoryProduct] = useState([])
    // console.log(products.filter(
    //   (product) => product.category.slug))
      console.log(categorySlug)
  
      useEffect(() => {
          if (categorySlug) {
              const filteredProduct = products.filter(
                (product) => product?.category.slug === categorySlug);
              setCategoryProduct(filteredProduct);
              // console.log(products?.category.slug )
              // console.log(slug)
          }
        }, [categorySlug, products]);

        // Log after state update
  useEffect(() => {
    console.log("Updated categoryProduct:", categoryProduct);
  }, [categoryProduct]);
  
    return (
      <section className='category_list'>
        <div className='container'>
          <ul className='product_list'>{
            categoryProduct.map((catpro)=>{
              return <li>
                  <Link href={`/product/${catpro.slug}`}>
                    <div className='img_wrap'>
                        <img src={catpro.images} />
                      </div>
                      <label className='cat_name'>{catpro.title}</label> 
                      <label className='cat_price'>â‚¹{catpro.price}</label> 

                      
                    </Link>
                </li>
          })}
          </ul>
          </div>
        </section>
    )
  }
  
  export default CategoryPage
